.body-container {
  min-height: 63vh;
}

.photo-box {
  height: 480px;
  min-width: 480px;
  border-radius: 50%;
  background: url('./pic.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

@media screen and (max-width: 480px) {
  .body-container {
    min-height: 33vh;
  }

  .photo-box {
    min-height: 200px;
    max-height: 350px;
    min-width: 350px;
    max-width: 350px;
    margin-left: 1.2em;
  }
}

.intro-box {
  min-width: 440px;
  height: 24em;
}

.intro-box #heading {
  font-size: 6em;
}

.intro-box #sub-heading {
  font-size: 2em;
}

.intro-box #description {
  font-size: 1em;
}

.footer-container .label {
  font-size: 1.1em;
  font-weight: bold;
}

.footer-container .label-value {
  font-size: 1em;
}

.footer-container .label-link {
  cursor: pointer;
}

/* Individual item */
.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
  border-bottom: 1px #d1d1d1 solid;
  padding: 10px;
}

/* Change color on hover */
.bm-item:hover {
  color: white;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 46px;
  height: 30px;
  right: 36px;
  top: 46px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #757779;
  height: 10% !important;
  width: 30px;
}

.bm-burger-button .bm-burger-bars:last-child, .bm-burger-button .bm-burger-bars:first-child {
  margin-right: 8px;
}

.bm-burger-button .bm-burger-bars:not(:last-child):not(:first-child) {
  margin-left: 8px;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

.bm-menu-wrap {
  width: 250px;
}

/* General sidebar styles */
.bm-menu {
  background: steelblue;
  padding: 2.5em 1.5em 0;
  font-size: 1.2em;  
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.header-menu {
  padding-top: 5px;
}

.header-menu li {
  display: inline-block;
  width: 100px;
  text-align: center;
}

.header-menu li:not(:last-child) {
  border-right: 1px gray solid;
}

.circular-btn {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  font-size: large;
}

.link, .link:hover, .link:visited, .link:active{
  text-decoration: none;
  color: inherit;
}